<template>
  <div id="app">
    <b-navbar type="light" variant="secondary" :sticky="true" style="z-index:99999999">
      <b-navbar-brand>
        <span>
          <a class="font-weight-bold" v-on:click="$router.currentRoute.name != main ? $router.back() : null" style="color:#ffffff;text-decoration: none;">
            <img v-show="$router.currentRoute.name != main" :src='icon' width="20px" height="20px" style="margin: -5px 10px 0 0;"/> 
            {{ $router.currentRoute.name }}
          </a>
        </span>
      </b-navbar-brand>
    </b-navbar>
    <div class="container" style="min-height:90vh;">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';
import Vue from 'vue';
import Toasted from 'vue-toasted';
import router from './router';
import bus from './bus';

Vue.use(Toasted);
// const bus = new Vue();

export default {
  name: 'app',
  data() {
    return {
      main: router.options.routes[0].name,
      icon: 'data:image/png;base64,',
    };
  },
  created() {
    bus.$on('bus', (state) => {
      if (state.notification !== undefined) {
        this.$toasted.show(`<span class="font-weight-normal">${state.notification.message}<span>`, {
          theme: 'primary',
          position: 'top-right',
          duration: 5000,
          className: `bg-${state.notification.type} text-white`,
        });
      }
    });
  },
};
</script>

<style>
</style>



